<style scoped>
  .drill {}
  
  .drillTop {
    margin: 10px;
  }
  
  .Nlanguage {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: rgba(0,0,0,.5);
  }
  .Nlanguage>.NLG {
    position: fixed;
    width: 66%;
    /* height: 100%; */
    top: 50%;
    left: 50%;
    background-color: white;
    margin-top: -17%;
    margin-left: -32%;
    z-index: 120;
    overflow: hidden;
    border-radius: 9px;
    padding: 15px;
  }
  .Nlanguage>.NLG>.left {
    float: left;
    width: 49%;
    text-align: center;
  }
  .Nlanguage>.NLG>.right {
    float: right;
    width: 49%;
  }
</style>

<template>
  <div class="drill">
    <Breadcrumb>
      <span slot="one"></span>
      <span slot="two"></span>
    </Breadcrumb>
    <div class="drillTop">
      <el-form :inline="true" :model="form" class="demo-form-inline" size="mini">
        <el-form-item label="">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-input v-model="form.operation" placeholder=""></el-input>
        </el-form-item>
        <el-form-item size="mini">
          <el-button type="primary" @click="onSubmit" icon="el-icon-search"></el-button>
        </el-form-item>
        <el-form-item size="mini">
          <el-button type="primary"></el-button><!-- @click="newTask"-->
        </el-form-item>
      </el-form>
      <!---->
      <div class="Nlanguage" v-show="nlguage">
        <div class="NLG">
          <h1 style="text-align: center;margin-bottom:10px;"></h1>
          <div class="left">
            <hr />
<el-form ref="formData" :rules="rules" :model="formData" label-width="80px">
  <el-form-item label="" prop="name" required>
    <el-input v-model="formData.name"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input type="textarea" v-model="formData.desc"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit"></el-button>
    <el-button @click="onEsc"></el-button>
  </el-form-item>
</el-form>
          </div>
          <div class="right">
                <div class="target_right">
                  <h1></h1>
<el-upload 
  class="upload-demo" 
  ref="upload" 
  action="http://192.168.1.118/sr/model/speech/add"
  :on-preview="handlePreview" 
  :on-remove="handleRemove" 
  :on-success="handleSuccess"
  :file-list="fileList" 
  :data="UpData"
  accept=".wav,.mp3,.flac,.ape"
  multiple
  :auto-upload="false">
  <el-button slot="trigger" size="small" type="primary"></el-button>
  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
  <div slot="tip" class="el-upload__tip">.WAV,.MP3,.FLAC,.APE</div>
</el-upload>

                  <!--<el-button type="primary"><i class="el-icon-upload el-icon--right"></i></el-button>-->
                  <el-button type="primary" :loading="xunlian" @click="drill"></el-button>
  <template>
    <el-table :data="speech" style="width: 100%">
      <el-table-column label="" width="180">
      </el-table-column>
      <el-table-column label="" width="180">
      </el-table-column>
      <el-table-column label="tsid">
      </el-table-column>
    </el-table>
  </template>
<div class="block">
  <!--<span class="demonstration"> 7 </span>-->
  <!--<el-pagination
    small
    page-size="5"
    layout="prev, pager, next"
    :total="1000">
  </el-pagination>-->
  
</div> 
</div>
          </div>
        </div>
      </div>
      <hr />
      <div class="drillmid">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="" width="180">
          </el-table-column>
          <el-table-column prop="date" label="" width="180">
          </el-table-column>
          <el-table-column prop="name" label="" width="180">
          </el-table-column>
          <el-table-column prop="address" label="">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb/Breadcrumb'
  export default {
    data() {
      return {
        nlguage:false,
        formData: {
          name: '',
          desc: ''
        },
         rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ]
         },
        form: {
          name: '',
          region: '',
          operation: ''
        },
        tableData: [
//      {
//          date: '2016-05-02',
//          name: '',
//          address: ' 1518 '
//        }, {
//          date: '2016-05-04',
//          name: '',
//          address: ' 1517 '
//        }, {
//          date: '2016-05-01',
//          name: '',
//          address: ' 1519 '
//        }, {
//          date: '2016-05-03',
//          name: '',
//          address: ' 1516 '
//        }
          ]
      }
    },
    methods: {
      onSubmit() {
//      console.log('submit!');
      },
      onEsc(){
        this.nlguage = false
      },
      newTask() {
        console.log('submit!');
        this.nlguage = true
      }
    },
    components: {
      Breadcrumb
    },
    computed: {

    }
  }
</script>


